<!--
 * @Descripttion: 登记/修改人员
 * @Author: 曾元球
 * @Date: 2019-07-02 16:43:46
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 16:44:25
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{name: 'CheckIn'}">登记台</el-breadcrumb-item>
        <el-breadcrumb-item v-if="prevRoute.isShow"
          :to="{name: prevRoute.name, query: prevRoute.query}">
          {{prevRoute.title}}
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          {{tjsysGuid === 'add' ? '登记人员' : '修改信息'}}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container flex-item">
      <div class="top">
        <div class="top-head">
          <i class="iconfont bj-person"></i>&nbsp;&nbsp;体检人员
        </div>
        <div class="top-content flex">
          <div class="avatar-box">
            <div class="avatar flex-center flex-v-center">
              <img v-if="imageUrl"
                :src="imageUrl" />
              <template v-else>
                <img src="@/assets/img/person-woman.png"
                  alt
                  v-if="personData.sexType==2" />
                <img src="@/assets/img/person-man.png"
                  alt
                  v-else />
              </template>
            </div>
            <p>要求：jpg/png格式<br>150 x 180效果最佳</p>
            <el-upload class="avatar-uploader"
              :action="uploadFileUrl"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :auto-upload="true"
              :before-upload="beforeAvatarUpload"
              :data='{projectName: "phy"}'>
              <el-button class="btn-info"
                size="small"
                style="width:150px">本地上传</el-button>
            </el-upload>
          </div>

          <el-form size="small"
            ref="personData"
            :rules="rules"
            :model="personData"
            label-width="7em"
            class="form-box">
            <el-row>
              <el-col :span="6">
                <el-form-item label="体检流水号："
                  class="form-item">{{examsysGuid | emptyData}}</el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="姓名："
                  class="form-item"
                  prop="userName">
                  <el-input placeholder="请输入"
                    v-model="personData.userName"
                    maxlength="31"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="性别："
                  class="form-item">
                  <el-select v-model="personData.sexType">
                    <el-option :value="1"
                      label="男"></el-option>
                    <el-option :value="2"
                      label="女"></el-option>
                    <el-option :value="3"
                      label="不详"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="出生日期："
                  class="form-item">
                  <el-date-picker type="date"
                    placeholder="选择日期"
                    style="width:100%"
                    v-model="personData.birthday"
                    value-format="yyyy-MM-dd"
                    :picker-options="{ disabledDate: time => time > new Date() }" />
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="体检次数："
                  class="form-item">
                  {{examNum}}
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="年龄："
                  class="form-item">
                  <el-input placeholder="请输入"
                    :value="personData.age"
                    @input="$outInt($event, 'age', personData)"
                    maxlength="3" />
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="民族："
                  class="form-item">
                  <el-input placeholder="请输入"
                    v-model="personData.nationCase"
                    maxlength="10"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="身份证号："
                  class="form-item"
                  prop="idNo">
                  <el-input placeholder="请输入"
                    v-model="personData.idNo"
                    maxlength="18"></el-input>
                </el-form-item>
              </el-col>

              <el-col :span="6">
                <el-form-item label="体检日期："
                  class="form-item">
                  <el-date-picker type="date"
                    placeholder="选择日期"
                    v-model="personData.examDate"
                    value-format="yyyy-MM-dd"
                    style="width:100%"></el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="户籍地址："
                  prop="residenceAddress">
                  {{personData.residenceAddress}}
                  <!-- <el-input placeholder="请输入"
                    v-model="personData.residenceAddress"
                    maxlength="51"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="6"
                style="height:51px;visibility:hidden;" />
              <el-col :span="6">
                <el-form-item label="操作医生："
                  class="form-item">{{personData.createUserName | emptyData}}</el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="现住址："
                  class="form-item long-ipt"
                  prop="liveAddress">
                  <el-input placeholder="请输入"
                    v-model="personData.liveAddress"
                    maxlength="51"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6"
                style="height:51px;visibility:hidden;" />
              <el-col :span="6">
                <el-form-item label="联系电话："
                  class="form-item"
                  prop="contactPhone">
                  <el-input placeholder="请输入"
                    v-model="personData.contactPhone"
                    maxlength="13"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="医疗机构："
                  class="form-item long-ipt">
                  {{personData.registerOrganization || personData.createOrganizationName}}
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
      </div>
      <div class="bottom">
        <div class="bottom-head">
          <i class="iconfont bj-menu"></i>&nbsp;&nbsp;体检项目
        </div>
        <div class="body flex">
          <div class="left-box">
            <h4>未选项目</h4>
            <div class="item-box">
              <div class="head-search">
                <el-input placeholder="请输入项目名称，套餐名称简码"
                  v-model="projectinput"
                  @change="getprojetData"></el-input>
                <span class="icon iconfont bj-search hand-shape"></span>
              </div>
              <div class="itembox"
                v-loading="loading">
                <div class="itemlist"
                  v-if="this.projectData.length>0">
                  <div class="itemtitle"
                    v-if="checkstatus==0">项目</div>
                  <div class="itemtitle"
                    v-if="checkstatus==1">常用项目</div>
                  <div class="ulitem">
                    <div class="liitem"
                      :class="{'active': handleMaxProjectActive(item)}"
                      @click="getevent(index,item)"
                      v-for="(item, index) in projectData"
                      :key="index">{{item.pname}}</div>
                  </div>
                </div>
                <div class="itemlist"
                  v-if="this.medicalData.length>0">
                  <div class="itemtitle"
                    v-if="checkstatus==0">套餐</div>
                  <div class="itemtitle"
                    v-if="checkstatus==1">常用套餐</div>
                  <div class="ulitem">
                    <div class="liitem"
                      v-for="(item, index) in medicalData"
                      :class="{'active': handleSetMealActive(item)}"
                      @click="getmedevent(index, item)"
                      :key="item.sysGuid">{{item.pname}}</div>
                  </div>
                </div>
                <div v-if="this.projectData.length<=0 && this.medicalData.length<=0"
                  style="padding-top:80px;text-align:center">暂无数据</div>
              </div>
            </div>
          </div>
          <div class="arrow-box flex-v-center">
            <div class="iconfont bj-arrow-right"></div>
          </div>
          <div class="right-box">
            <h4>已选项目</h4>
            <dl class="flex-v item-box">
              <dt class="flex">
                <div class="first-item"></div>
                <div class="index">序号</div>
                <div class="flex flex-item">
                  <div class="dt-title">检查项目</div>
                  <div class="dt-title">项目名称</div>
                  <div class="dt-title">体检状态</div>
                  <div class="dt-title">数据传输</div>
                </div>
                <div class="last-item"></div>
              </dt>
              <dd class="flex-item"
                v-show="selectedlist.length">
                <el-collapse value="1">
                  <el-collapse-item :name="index"
                    v-for="(item, index) in selectedlist"
                    :key="index">
                    <template slot="title">
                      <div class="first-item"
                        @click.stop="closeItem(item)">
                        <i class="el-icon-circle-close"></i>
                      </div>
                      <div class="index">{{index + 1}}</div>
                      <div class="flex flex-item">
                        <div class="text-item">{{item.pname}}</div>
                      </div>
                      <div class="last-item"></div>
                    </template>
                    <div class="flex-v-center sub-item"
                      v-for="(e, i) in item.smallProjects"
                      :key="i">
                      <div class="first-item"
                        @click.stop="closeItem(e, item)">
                        <i class="el-icon-circle-close"></i>
                      </div>
                      <div class="index"></div>
                      <div class="flex flex-item">
                        <div class="text-item">{{e.pname}}</div>
                        <div class="text-item">
                          {{e.isCheck == 1 ? '已检查' : '未检查'}}
                        </div>
                        <div class="text-item">未同步</div>
                      </div>
                      <div class="last-item"></div>
                    </div>
                  </el-collapse-item>
                </el-collapse>
              </dd>
            </dl>
          </div>
        </div>
      </div>
      <footer class="btn-box">
        <el-button class="btn-hollowing"
          @click="$router.back()">取消</el-button>
        <el-button class="btn-info"
          @click="goCheckInDetail"
          :loading="submitLoading">保存</el-button>
        <!-- <el-button class="btn-red">打印指引单</el-button> -->
      </footer>
    </div>
  </div>
</template>

<script>
import { formatDate, lenRule, regPhone, regIdNo, concatUploadFileUrl } from '@/lib/utils'

function deepCopy(data) {
  // 深度克隆对象
  return JSON.parse(JSON.stringify(data))
}
export default {
  name: 'CheckInPersonnel',
  data() {
    return {
      prevRoute: {},
      routelist: [],
      sysGuid: '',
      tjsysGuid: '',
      examsysGuid: '',
      checkstatus: 1,
      pagestatic: '', //页面标识
      examNum: 0,
      personData: {
        userName: '',
        sexTypeStr: '',
        sexType: '',
        birthdayStr: '',
        birthday: '',
        age: '',
        nationCase: '',
        idNo: '',
        examDate: '',
        residenceAddress: '',
        liveAddress: '',
        contactPhone: '',
        registerOrganization: '',
        createOrganizationName: '',
        sysGuid: '-',
        createUserName: '',
        personType: '',
        historyTraumaJson: '',
        historyTransfusionJson: '',
        historyOperationJson: '',
        historyIllnessJson: '',
        historyFamilyJson: ''
      },
      medicalData: [],
      projectData: [],
      isactivelist: [],
      ismedactivelist: [],
      projectinput: '',
      selectedlist: [],
      projectIdslist: [], //每个选中小项的id
      packIdList: [], //每个套餐项目ID
      imageUrl: '',
      pySimpleCode: '',
      bigProject: [], //大项
      smallProject: [], //小项
      setMealObj: {},
      sex: '',
      rules: {
        userName: [{ required: true, message: '请输入姓名', trigger: 'blur' }, ...lenRule(30)],
        idNo: {
          pattern: regIdNo,
          message: '请输入正确的身份证号码',
          trigger: 'blur'
        },
        contactPhone: {
          pattern: regPhone,
          message: '请输入正确的电话号码',
          trigger: 'blur'
        },
        residenceAddress: [...lenRule()],
        liveAddress: [...lenRule()]
      },
      loading: true,
      submitLoading: false,
      uploadFileUrl: concatUploadFileUrl('/common/file/uploadFile')
    }
  },
  created() {
    console.log(this.uploadFileUrl)
    this.oftenUse = Object.create(null) // 把初始常用套餐和项目缓存
    this.projectObj = Object.create(null) // 储存项目所属套餐id
    this.sysGuid = this.$route.query.sysGuid
    this.tjsysGuid = this.$route.query.tjsysGuid
    this.getusedData()
  },

  beforeRouteEnter(to, from, next) {
    let { name, query } = from
    let title = from.meta.title
    let showNames = ['CheckInDetail', 'PersonList']
    next(vm => {
      vm.prevRoute = {
        name,
        title,
        query,
        isShow: ~showNames.indexOf(name)
      }
    })
  },
  methods: {
    // 修改时获取体检信息
    getData() {
      let url = '/sys/project/getShowExam'
      this.$post(url, { sysGuid: this.tjsysGuid })
        .then(({ data }) => {
          let { user } = data
          this.getExamNum(user.idNo)
          Object.keys(this.personData).forEach(key => {
            this.personData[key] = user[key]
          })
          this.personData.birthday = formatDate(user.birthday)
          this.personData.examDate = formatDate(new Date())
          this.examsysGuid = user.sysGuid
          this.imageUrl = user.addOn1

          this.bigProject = data.bigProject
          this.smallProject = data.smallProject
          this.selectedlist = data.bigProject.map(item => {
            let i = this.projectIdslist.indexOf(item.sysGuid)
            i === -1 && this.projectIdslist.push(item.sysGuid)
            let o = {
              pname: item.pname,
              sysGuid: item.sysGuid,
              smallProjects: []
            }
            data.smallProject.forEach(items => {
              items.sysGuid = items.projectId // 初始数据和查询的数据字段不一样，这里做个统一
              let idx = this.projectIdslist.indexOf(items.projectId)
              idx === -1 && this.projectIdslist.push(items.projectId)
              if (item.sysGuid === items.parentId) o.smallProjects.push(items)
            })
            return o
          })
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    getExamNum(idNo) {
      this.$post('/user/examUser/getExamTimes', {
        idNo,
        type: 'total'
      })
        .then(({ data }) => {
          this.examNum = data.split(',')[0]
        })
        .catch(err => err)
    },

    getpersonData() {
      if (this.tjsysGuid !== 'add') {
        return this.getData()
      }
      let url = 'user/getPersionDoc/getByPage'
      const params = {
        sysGuid: this.sysGuid,
        pageNum: 1,
        pageSize: 1
      }
      this.$post(url, params)
        .then(({ data }) => {
          let user = data.list[0]
          this.getExamNum(user.idNo)
          Object.keys(this.personData).forEach(key => {
            this.personData[key] = user[key]
          })
          this.personData.createOrganizationName = user.registerOrganization
          this.personData.birthday = formatDate(user.birthday)
          let residenceArr = []
          let liveArr = []
          ;['ProvinceName', 'CityName', 'CountyName', 'TownName', 'VillageName'].forEach(item => {
            if (user[`residence${item}`]) residenceArr.push(user[`residence${item}`])
            if (user[`live${item}`]) liveArr.push(user[`live${item}`])
          })
          this.personData.residenceAddress = residenceArr.join('')
          this.personData.liveAddress = liveArr.join('')
          this.personData.contactPhone = user.mobile
          this.personData.examDate = formatDate(new Date())
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    //获取常用套餐和项目
    getusedData() {
      let url = '/sys/projectPackage/findUserHabitsProjectAndPackage'
      this.medicalObj = Object.create(null) // 创建一个没有prototype的对象
      this.$post(url)
        .then(({ data }) => {
          this.loading = false
          this.medicalData = data.packPage.list.map(item => {
            item.isactive = false
            this.medicalObj[item.packId] = 0 // 初始化套餐中的项目数量
            return { isactive: false, pname: item.pname, sysGuid: item.packId }
          })
          this.projectData = data.projPage.list.map(item => {
            return {
              isactive: false,
              pname: item.pname,
              sysGuid: item.projectId
            }
          })
          this.oftenUse.medicalData = deepCopy(this.medicalData)
          this.oftenUse.projectData = deepCopy(this.projectData)
          this.getpersonData()
        })
        .catch(error => {
          error && this.$message.error(error)
        })
    },

    // 选中项目处理
    getevent(index, item) {
      if (item.isactive) return
      let url = '/sys/project/getBigProjectSmallProject'
      this.$post(url, { projectId: item.sysGuid })
        .then(({ data }) => {
          if (data.smallProjects.length === 0) {
            return this.$message.warning('该项目下没有小项')
          }
          item.isactive = true
          if (this.selectedlist.findIndex(item => item.sysGuid === data.sysGuid) === -1) {
            this.selectedlist.push(data)
            this.projectIdslist = this.projectIdslist.concat(
              data.sysGuid,
              data.smallProjects.map(item => item.sysGuid)
            )
          }
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    // 选中套餐处理
    getmedevent(index, item) {
      if (item.isactive) return
      let url = '/sys/projectPackage/getThisPackProject'
      let packId = item.sysGuid
      this.$post(url, { packId })
        .then(({ data }) => {
          if (data.projects.length === 0) {
            return this.$message.warning('该套餐下没有项目')
          }

          // 储存套餐中的项目数量，删除时会递减
          this.medicalObj[packId] = data.projects.length

          // 已选套餐集合
          this.packIdList.indexOf(packId) === -1 && this.packIdList.push(packId)
          item.isactive = true
          let projectLen = 0

          // 遍历项目
          data.projects.forEach(it => {
            // 储存套餐id 项目可能属于多个套餐，不一定是属于一个套餐
            this.projectObj[it.sysGuid] = this.projectObj[it.sysGuid] || []
            if (-1 === this.projectObj[it.sysGuid].indexOf(packId)) {
              this.projectObj[it.sysGuid].push(packId)
            }
            it.packageIds = this.projectObj[it.sysGuid]
            let idx = this.selectedlist.findIndex(_item => _item.sysGuid === it.sysGuid)
            if (~idx) {
              projectLen++
              let o = this.selectedlist[idx]
              o.packageIds = it.packageIds
              this.selectedlist.splice(idx, 1, o)
            } else {
              this.selectedlist.push(it)
              this.projectIdslist = this.projectIdslist.concat(
                it.sysGuid,
                it.smallProjects.map(item => item.sysGuid)
              )
            }
          })
          if (data.projects.length === projectLen) {
            this.$message('此套餐中的项目已被全部选中！')
          }
          console.log(this.projectIdslist)
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    // 处理大项高亮
    handleMaxProjectActive(item) {
      let id = item.projectId || item.sysGuid
      let isActive = ~this.projectIdslist.indexOf(id)
      item.isactive = isActive
      return isActive
    },

    // 处理套餐高亮
    handleSetMealActive(item) {
      let id = item.packId || item.sysGuid
      let isActive = ~this.packIdList.indexOf(id)
      item.isactive = isActive
      return isActive
    },

    //模糊查询体检项目
    fuzzyserchData() {
      let url = '/sys/projectPackage/searchPackAndProject'
      const params = { keyword: this.projectinput }
      this.$post(url, params)
        .then(({ data }) => {
          this.loading = false
          this.medicalData = data.projectPackPage.list.map(it => {
            let { packName, sysGuid } = it
            // 引用或初始化套餐中的项目数量
            this.medicalObj[sysGuid] = this.medicalObj[sysGuid] || 0
            return { isactive: false, pname: packName, sysGuid }
          })
          this.projectData = data.projectPage.list.map(it => {
            return { isactive: false, pname: it.pname, sysGuid: it.sysGuid }
          })
        })
        .catch(error => {
          this.loading = false
          this.$message.error(error)
        })
    },

    //搜索套餐和项目
    getprojetData() {
      if (this.projectinput) {
        this.loading = true
        this.checkstatus = 0
        this.fuzzyserchData()
      } else {
        this.checkstatus = 1
        this.medicalData = deepCopy(this.oftenUse.medicalData)
        this.projectData = deepCopy(this.oftenUse.projectData)
      }
    },

    // 删除已选中的大项或小项
    closeItem(item, maxItem) {
      let { sysGuid } = item
      if (maxItem) {
        if (item.isCheck == 1) return this.$message.warning('此小项已检查，不能删除！')
        let i = maxItem.smallProjects.findIndex(it => it.sysGuid === sysGuid)
        let j = this.projectIdslist.indexOf(sysGuid)
        this.projectIdslist.splice(j, 1) // 删除小项id
        maxItem.smallProjects.splice(i, 1) // 删除小项
        if (0 === maxItem.smallProjects.length) {
          let idx = this.selectedlist.findIndex(e => e.sysGuid === maxItem.sysGuid),
            idx1 = this.projectIdslist.indexOf(maxItem.sysGuid),
            packageIds = maxItem.packageIds
          this.selectedlist.splice(idx, 1) // 删除大项
          this.projectIdslist.splice(idx1, 1) // 删除大项id
          if (packageIds) {
            // 如果有套餐id
            packageIds.forEach(id => {
              this.medicalObj[id]--
              if (0 === this.medicalObj[id]) {
                // 如果套餐中的项目已清空
                let packageIndex = this.packIdList.indexOf(id)
                ~packageIndex && this.packIdList.splice(packageIndex, 1)
              }
            })
          }
        }
      } else {
        console.log(this.projectIdslist)
        if (item.smallProjects.some(item => item.isCheck == 1)) {
          return this.$message.warning('有小项已检查，不能删除！')
        }

        // 删除大项id
        let idx = this.projectIdslist.indexOf(sysGuid)
        ~idx && this.projectIdslist.splice(idx, 1)

        // 删除该大项中的所有小项id
        item.smallProjects.forEach(e => {
          console.log(e)
          let i = this.projectIdslist.indexOf(e.sysGuid)
          ~i && this.projectIdslist.splice(i, 1)
        })
        if (item.packageIds) {
          // 如果删除的是套餐下的项目
          item.packageIds.forEach(id => {
            this.medicalObj[id]--
            if (0 === this.medicalObj[id]) {
              // 如果套餐中的项目已清空
              let packageIndex = this.packIdList.indexOf(id)
              ~packageIndex && this.packIdList.splice(packageIndex, 1)
            }
          })
        }

        // 删除大项
        let maxIndex = this.selectedlist.findIndex(e => e.sysGuid === sysGuid)
        ~maxIndex && this.selectedlist.splice(maxIndex, 1)
        console.log(this.projectIdslist)
      }
    },

    // 验证表单
    goCheckInDetail() {
      this.$refs['personData'].validate(valid => {
        if (!valid) return
        this.postCheckInData()
      })
    },

    // 提交数据
    postCheckInData() {
      let url = ''
      const params = {
        ...this.personData,
        addOn1: this.imageUrl,
        packIds: this.packIdList.toString(),
        projectIds: this.projectIdslist.toString()
      }
      if ('add' === this.tjsysGuid) {
        url = '/user/examUser/insert'
      } else {
        url = '/user/examUser/updateUserExamItem'
        params.sysGuid = this.tjsysGuid
      }
      if (0 === this.projectIdslist.length) return this.$message.warning('至少选择一个项目！')
      this.submitLoading = true
      this.$post(url, params)
        .then(({ data }) => {
          this.submitLoading = false
          this.tjsysGuid = data.sysGuid
          this.$router.push({
            name: 'CheckInDetail',
            query: {
              tjsysGuid: data.sysGuid,
              sysGuid: this.sysGuid
            }
          })
        })
        .catch(error => {
          this.submitLoading = false
          this.$message.error(error)
        })
    },
    //图片上传
    handleAvatarSuccess({ data }) {
      this.imageUrl = concatUploadFileUrl(data.filePath)
    },

    // 上传前拦截（可限制格式和大小）
    beforeAvatarUpload(file) {
      const isJPG = ~['image/jpeg', 'image/png', 'image/jpg'].indexOf(file.type)
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.warning('上传头像图片只能是jpg/png格式!')
      }
      if (!isLt2M) {
        this.$message.warning('上传头像图片大小不能超过2MB!')
      }
      return isJPG && isLt2M
    }
  }
}
</script>

